import { findIndex, includes, isEqual, uniq } from 'lodash';

// ---------------------------------------- Globals //
const mouseEvents = [
	'click', // Works with multiple single clicks pattern
	'dblclick', // Only works with single double click pattern set
	'mouseup', // Works with multiple mouseup clicks pattern
	'mousedown', // Works with multiple mousedown clicks pattern
];
const easterEggsTriggerEggs = [];
let $this = null;

const EasterEggTriggerCore = {
	// ---------------------------------------- Defaults //
	pluginOptions: {
		callback: null,
		delay: 500,
		destroyBus: false,
		name: 'easter-egg',
		pattern: ['ArrowUp', 'ArrowUp', 'ArrowDown', 'ArrowDown', 'ArrowLeft', 'ArrowRight', 'ArrowLeft', 'ArrowRight', 'b', 'a'],
		target: 'div',
		triggered: null,
		type: 'keydown',
		withBus: true,
	},
	input: [],
	methodOptions: {},
	options: {},
	targets: {
		nodes: [],
		ids: [],
		classNames: [],
	},
	timeout: () => { },

	/*
		---------------------------------------- Init
		? Initializes the plugin
	*/
	init(globalOptions, methodOptions) {
		console.group('EasterEggTriggerCore', '------------------------------- Core Init');
		console.log({ globalOptions, methodOptions });

		// Merge user and global //
		this.pluginOptions = { ...this.pluginOptions, ...globalOptions, ...methodOptions };
		$this = this;

		console.log(this.pluginOptions);
		this.layEggs();
	},

	/*
		---------------------------------------- Lay Eggs
		? Adds Eggs to Easter Eggs
	*/
	layEggs() {
		easterEggsTriggerEggs.push(this.pluginOptions);
		console.log('------------------------------- layEggs', { easterEggsTriggerEggs });

		this.collectEggs();
	},

	/*
		---------------------------------------- Collect Eggs
		? Add eggs to global easterEggsTriggerEggs array
	*/
	// Loop through eggs and call add event listener //
	collectEggs() {
		console.log('------------------------------- collectEggs', easterEggsTriggerEggs.length);
		Object.values(easterEggsTriggerEggs).forEach((egg) => {
			console.log(egg.name, { egg });
			const newEgg = egg;

			if (!newEgg.pattern) {
				newEgg.pattern = this.pluginOptions.pattern;
			}

			if (!newEgg.target) {
				newEgg.target = this.pluginOptions.target;
			}
		});

		this.addListener();
	},

	/*
		---------------------------------------- Add Listener
		? Create and add event listener
	*/
	addListener() {
		console.log('------------------------------- addListener', this.pluginOptions.type);

		document.addEventListener(this.pluginOptions.type, this.capturePattern, false);
	},

	/*
		---------------------------------------- Capture Pattern
		? Capture the Keys or Click Pattern
	*/
	capturePattern(e) {
		console.log('------------------------------- capturePattern', { e });
		console.log({ $this });
		let key = '';
		// console.log({ e });

		if ($this.timeout !== null) {
			clearTimeout($this.timeout);
		}

		// -------------------- Keyboard Events //
		if (e.key !== undefined) {
			key = e.key;
		}

		// // -------------------- Mouse Events //
		// if (_.includes(EasterEggTrigger.mouseEvents, e.type)) {
		// 	console.log('includes');
		// 	const target = e.target;
		// 	key = e.type;

		// 	EasterEggTrigger.targets.nodes.push(target.nodeName.toLowerCase());
		// 	EasterEggTrigger.targets.ids.push(target.id);
		// 	EasterEggTrigger.targets.classNames.push(target.classList.value);
		// }

		// console.log({ key });

		$this.input.push(key);
		$this.checkPattern(e);

		console.log({ key });

		return false;
	},

	/*
		---------------------------------------- Check Pattern
		? Check the Keys or Click Pattern
	*/
	checkPattern(e) {
		console.log('------------------------------- checkPattern', this);
		console.log({ easterEggsTriggerEggs, e });
		console.log('\n');

		Object.values(easterEggsTriggerEggs).forEach((egg) => {
			console.log({ egg }, this.input);

			// Check Keyboard Events //
			console.log('egg.pattern', egg.pattern);
			console.log('this.input', this.input);
			console.log('isEqual(egg.pattern, this.input)', isEqual(egg.pattern, this.input));

			if (isEqual(egg.pattern, this.input)) {
				// Check Targets if Mouse Events //
				if (includes($this.mouseEvents, e.type)) {
					this.checkTarget(e, egg);
					$this.hatched('1');
					return false;
				}

				$this.hatched('2');
				// this.emitEvent(egg);
			}

			return false;
		});


		// this.reset();
		return false;
	},

	/*
		---------------------------------------- Check Target Element
		? Checks the target
	*/
	checkTarget(e, egg) {
		console.log({ egg });
		// Get clean egg target //
		const node = egg.target;
		const id = egg.target.replace('#', '');
		const className = egg.target.replace('.', '');

		console.log({ className });

		// Reduce targets to unique values //
		const nodes = uniq(this.targets.nodes);
		const ids = uniq(this.targets.ids);
		const classNames = uniq(this.targets.classNames);

		console.log({ nodes, ids, classNames });

		// Targets array should reduce down to one value, and match the clean egg target //
		const nodeTargetsMatch = nodes.length === 1 && nodes[0] === node;
		const idTargetsMatch = ids.length === 1 && ids[0] === id;
		const classTargetsMatch = classNames.length === 1 && includes(classNames[0], className);

		console.log({ nodeTargetsMatch, idTargetsMatch, classTargetsMatch });

		if (nodeTargetsMatch || idTargetsMatch || classTargetsMatch) {
			this.emitEvent(egg);
		}

		// this.reset();
		$this.hatched('checkTarget');
	},

	/*
	 ---------------------------------------- Hatch
	? Easter Egg Hatched
	*/
	hatched(n = 'last') {
		console.log('HATCHED', n);
		console.groupEnd('EasterEggTriggerCore');
	},
};

export default EasterEggTriggerCore;
